/*
General Layout: 2% boarder from top
Main Body: 64% wide, 5% from the top, 60% height, 

Clock: 18% width, 10% height, 2% from the top, left side





*/

body {
    background-color: black;
    font-family: "Futura PT", sans-serif;
}

.App {
    text-align: center;
}

.App-logo {
    height: 40vmin;
    pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
    .App-logo {
        animation: App-logo-spin infinite 20s linear;
    }
}

@font-face {
    font-family: "Futura PT";
    src: url("../FuturaCyrillicLight.ttf") format("woff2");
    font-weight: 300; /* Light */
    font-style: normal;
}

/*Tab Overall Container*/
.upper-tab-container {
    display: flex;
    width: 64%;
    position: absolute;
    top: 2%;
    height: 3%;
    left: 18%;
    gap: 0;
}

/*Start tab*/
.starter-tab-button:hover {
    background-color: rgba(
        255,
        255,
        255,
        0.1
    ); /* Lighten background on hover */
    color: #fff; /* Change text color on hover */
}

.starter-shell-container {
    width: 20%;
    height: 100%;
    position: relative;
}

.starter-tab-shell-outer {
    width: 100%;
    height: 100%;
    background-color: white;
    clip-path: polygon(0% 0%, 80% 0%, 100% 100%, 0% 100%);
    position: absolute;
    border-radius: 10px 0px 0px 0px;
    justify-content: center;
}

.starter-tab-shell-inner {
    width: 100%;
    height: 100%;
    background-color: rgb(0, 0, 0);
    clip-path: polygon(0% 0%, 80% 0%, 100% 100%, 0% 100%);
    position: absolute;
    border-radius: 10px 0px 0px 0px;
    justify-content: center;
    top: 2%;
    left: 1%;
}

.starter-tab-button {
    width: 95%;
    height: 100%;
    justify-content: center;
    font-size: 100%;
    clip-path: polygon(0% 0%, 80% 0%, 100% 100%, 0% 100%);
    font-family: "Futura PT", sans-serif;
    border-radius: 10px 0px 0px 0px;
    position: absolute;
    top: 2%;
    left: 1%;
    background-color: transparent;
    cursor: pointer;
}

/*Follower tab*/

.follower-tab-button:hover {
    background-color: rgba(
        255,
        255,
        255,
        0.1
    ); /* Lighten background on hover */
    color: #fff; /* Change text color on hover */
}

.follower-shell-container {
    width: 20%;
    height: 100%;
    position: relative;
    margin-left: -4.5%;
}

.follower-tab-shell-outer {
    width: 100%;
    height: 100%;
    background-color: white;
    clip-path: polygon(0% 0%, 80% 0%, 100% 100%, 20% 100%);
    position: absolute;
    justify-content: center;
}

.follower-tab-shell-inner {
    width: 96%;
    height: 98%;
    background-color: black;
    clip-path: polygon(0% 0%, 80% 0%, 100% 100%, 20% 100%);
    position: absolute;
    top: 2%;
    left: 2%;
    justify-content: center;
    z-index: 1;
}
.follower-tab-button {
    width: 95%;
    height: 100%;
    justify-content: center;
    font-size: 100%;
    clip-path: polygon(0% 0%, 80% 0%, 100% 100%, 20% 100%);
    font-family: "Futura PT", sans-serif;
    position: absolute;
    top: 2%;
    left: 1%;
    background-color: transparent;
    z-index: 2;
    cursor: pointer;
}

/*Module Left*/
.module-left-header {
    width: 18%;
    cursor: default;
    height: 3%;
    position: absolute;
    left: 0%;
    top: 13%;
    border: 1px solid white;
    background-color: black;
    color: white;
}

.module-left {
    cursor: default;
    width: 18%;
    height: 49%;
    position: absolute;
    left: 0%;
    top: 16%;
    background-color: rgb(26, 26, 26);
    color: white;
    padding: 0.75%;
}

/*Clock Block*/
.Clock {
    display: flex;
    position: absolute;
    top: 2%;
    width: 18%;
    height: 10%;
    left: 0%;
    font-size: 400%;
    align-items: center;
    justify-content: center;
    color: white;
    background-color: black;
    border-color: white;
    font-family: "Futura PT", sans-serif;
    cursor: default;
}

/*Main Console*/
.App-header {
    position: absolute;
    top: 5%;
    left: 50%;
    transform: translateX(-50%);
    width: 64%;
    background-color: black;
    border: 1px solid white;
    height: 60%;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    font-size: calc(10px + 2vmin);
    color: white;
    border-radius: 0 0 10px 10px;
    cursor: default;
}

/*App link that I don't use?*/
.App-link {
    color: #61dafb;
}

@keyframes App-logo-spin {
    from {
        transform: rotate(0deg);
    }
    to {
        transform: rotate(360deg);
    }
}
